"use client"


import ApiContext from "@/Context/ProductApi/ApiContext";
import { useParams } from "next/navigation";
import { useContext, useEffect, useState } from "react";
import '../productDetail.scss'
import Quantity from "@/Components/Quantity/Quantity";
import CartButton from "@/Components/CartButton/Button";

const DetailPage = () =>{
    const param = useParams(ApiContext);
    const {productSlug} = param
    const {products} = useContext(ApiContext)
    const [product, setProduct] = useState(null);
    useEffect(()=>{
        if(productSlug && products?.length > 0){
            const filterProduct = products.find((pro)=> pro.slug === productSlug);
            setProduct(filterProduct || null);
        }
        
    },[productSlug,products])
    if (!product) {
        return <p>Loading product details...</p>;
    }
    return(
        <section className="product_detail">
            <div className="container">
                <div className="inner_">
                    <div className="image_sec">
                        {/* {product.images?.length > 0 ? (
                            <img src={product.images[0]} alt={product.title} />
                        ) : (
                            <p>No image available</p>
                        )} */}
                        {product?.images?.map((img,index)=>(
                            <img key={index} src={img} alt={product.title} />
                        ))}
                    </div>
                    <div className="content_sec">
                        <label className="detail_title">{product.title}</label>
                        <label className="detail_price">â‚¹{product.price}</label>
                        <Quantity productId={product.id}/>
                        <CartButton productCart={product.id}/> 
                    </div>
                </div>
            </div>
        </section>
    )
    
}
export default DetailPage;